.flatButton{
    position: relative;
    cursor: pointer;
    overflow: hidden;
    font-weight: bold;
}

.flatButton::before{
    content: attr(data-text);
	width: 100%;
    height: 100%;
	position: absolute;
	z-index: 2;
    right: 0;
    color: var(--backgroundEmphasis);
    opacity: 0;
	transition: .6s ease-in-out all;
}

.flatButton::after {
	content: "";
	width: 100%;
	position: absolute;
	z-index: 1;
	top: 0;
	left: calc(-100% - 80px);
	border-bottom: 80px solid var(--backgroundBase);
	border-right: 80px solid transparent;
	transition: .4s ease-in-out all;
}

.flatButton:hover::after {
	left: 0;
}
.flatButton:hover::before{
    opacity: 1;
}


